<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>tilingScheme</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> The tiling scheme for which to draw tiles.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The ellipsoid. If the tilingScheme is specified, this parameter is ignored and the tiling scheme's ellipsoid is used instead. If neither parameter is specified, the WGS84 ellipsoid is used.</td>
</tr>
<tr>
<td>cells</td>
<td>Number</td>
<td><code v-pre>8</code></td>
<td><code v-pre>optional</code> The number of grids cells.</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre>[1.0, 1.0, 1.0, 0.4]</code></td>
<td><code v-pre>optional</code> The color to draw grid lines.</td>
</tr>
<tr>
<td>glowColor</td>
<td>Object|String|Array</td>
<td><code v-pre>[0.0, 1.0, 0.0, 0.05]</code></td>
<td><code v-pre>optional</code> The color to draw glow for grid lines.</td>
</tr>
<tr>
<td>glowWidth</td>
<td>Number</td>
<td><code v-pre>6</code></td>
<td><code v-pre>optional</code> The width of lines used for rendering the line glow effect.</td>
</tr>
<tr>
<td>backgroundColor</td>
<td>Object|String|Array</td>
<td><code v-pre>[0.0, 0.5, 0.0, 0.2]</code></td>
<td><code v-pre>optional</code> Background fill color.</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code> The width of the tile for level-of-detail selection purposes.</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code> The height of the tile for level-of-detail selection purposes.</td>
</tr>
<tr>
<td>canvasSize</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code> The size of the canvas used for rendering.</td>
</tr>
</tbody>
</table>
